.table {
  margin-top: 10px;
  overflow: scroll;
  height: 350px;
}
.table tr {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.table td {
  padding: 0.6rem;
}
.table tr:nth-of-type(odd) {
  background-color: #0f8d48bb;
  color: rgb(255, 255, 255);
}

.news {
  margin-top: 10px;
  overflow: scroll;
  height: 180px;
}
