.footer {
  height: 30vh;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}
h1 {
  font-weight: 500;
  font-size: 2rem;
  margin-bottom: 0rem;
  letter-spacing: 1px;
  word-spacing: 2px;
  margin-bottom: 0.7rem;
}
p {
  font-size: 0.9rem;
}
.social_links {
  padding: 0.2rem;
  background: #05386B;
  border-radius: 1rem;
  margin-top: 1rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}
.social_links .combo {
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
  transition: 0.4s ease-in;
  margin: 0 0.5rem;
  height: 2em;
  width: 2em;
  line-height: 2em;
}
.combo .circle {
  color: #EDF5E1;
  transition: 0.4s ease-in;
  font-size: 2em;
}
.combo .icon {
  color: #05386B;
  font-size: 1.5rem;
  -webkit-transform: translateY(1px);
          transform: translateY(1px);
  transition: 0.2s ease-in;
}
.combo:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.combo:hover,
.combo:hover .icon {
  color: white;
}
.ig:hover,
.ig:hover .circle {
  color: #e91e63;
}
.fb:hover,
.fb:hover .circle {
  color: #2196f3;
}
.yt:hover,
.yt:hover .circle {
  color: #f44336;
}
.tw:hover,
.tw:hover .circle {
  color: #2196f3;
}
.db:hover,
.db:hover .circle {
  color: #ffc107;
}
.ml:hover,
.ml:hover .circle {
  color: #b6854d;
}
.gt:hover,
.gt:hover .circle {
  color: #505050;
}

*,
*:before,
*:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 100%;
  font-weight: normal;
  line-height: 1.5;
}

a {
  text-decoration: none;
}

.container {
  max-width: 1400px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.header {
  position: relative;
  width: 100%;
  height: 50px;
  line-height: 50px;
}

.header .logo {
  font-size: 1.5em;
}

.header .logo a {
  color: #000;
  text-decoration: none;
}

/* Mobile menu */
#menu-icon {
  display: block;
  position: absolute;
  top: 0;
  right: 1em;
  color: #000;
  text-decoration: none;
  font-weight: bold;
  cursor: pointer;
  opacity: 1;
  padding: 0 0.4em;
}

#menu-icon:hover {
  opacity: 0.5;
  text-decoration: none;
}

.navbar {
  float: none;
  max-height: 0;
  overflow: hidden;
}

.navbar .menu {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.navbar .menu li {
  list-style-type: none;
  display: block;
}

.navbar .menu li a {
  display: block;
  text-decoration: none;
  color: #000;
  padding: 0 0.8em;
}
.header .expand {
  max-height: 20em;
  display: block;
  overflow: hidden;
}
.expand {
  background-color: rgb(255, 255, 255);
}
.expand li:hover {
  color: rgb(255, 255, 255);
  background-color:rgba(0, 0, 0, 0.11);
  text-decoration: none;
}
.mr {
  margin-bottom: 7rem;
}

@media only screen and (min-width: 1000px) {
  .logo {
    margin-left: 0;
  }

  .logo,
  .navbar {
    display: inline-block;
  }
  .header {
    margin-bottom: 2rem;
  }

  /* Mobile menu icon */
  #menu-icon {
    display: none;
  }

  .navbar {
    float: right;
    max-height: none;
  }

  .navbar .menu li {
    list-style-type: none;
    display: inline-block;
    text-align: center;
  }

  .navbar .menu li a {
    display: block;
    border-bottom: 0;
  }
}

